<template>
  <section class="mx-auto max-w-3xl px-4 py-10">
    <!-- Toast -->
    <div
      v-if="toast.show"
      class="fixed right-4 top-4 z-50 max-w-sm rounded-2xl border border-black/10 bg-white px-4 py-3 shadow-lg"
    >
      <div class="flex items-start justify-between gap-3">
        <div>
          <p class="text-sm font-semibold text-black">
            {{ toast.title }}
          </p>
          <p class="mt-0.5 text-sm text-black/60">
            {{ toast.message }}
          </p>
        </div>
        <button class="text-black/40 hover:text-black" @click="toast.show = false">✕</button>
      </div>
    </div>

    <form
      class="rounded-[28px] border border-black/10 bg-white p-6 shadow-sm"
      @submit.prevent="submitOrder"
    >
      <div class="grid gap-4 sm:grid-cols-2">
        <div>
          <label class="text-xs font-semibold text-black/60">Nume</label>
          <input
            v-model.trim="form.customerName"
            class="mt-2 w-full rounded-2xl border border-black/10 px-4 py-3 text-sm outline-none focus:ring-4 focus:ring-black/10"
            placeholder="Nume"
            required
          />
        </div>

        <div>
          <label class="text-xs font-semibold text-black/60">Telefon</label>
          <input
            v-model.trim="form.phone"
            class="mt-2 w-full rounded-2xl border border-black/10 px-4 py-3 text-sm outline-none focus:ring-4 focus:ring-black/10"
            placeholder="07xx xxx xxx"
            required
          />
        </div>
      </div>

      <div class="mt-5">
        <label class="text-xs font-semibold text-black/60">Produse</label>
        <div class="mt-3 space-y-3">
          <div
            v-for="(item, index) in form.items"
            :key="`${item.name}-${index}`"
            class="grid gap-3 rounded-2xl border border-black/10 bg-black/5 p-3 sm:grid-cols-[1.4fr,0.6fr,0.6fr,auto]"
          >
            <select
              v-model="item.name"
              class="w-full rounded-2xl border border-black/10 bg-white px-4 py-3 text-sm outline-none focus:ring-4 focus:ring-black/10"
            >
              <option v-for="name in itemNames" :key="name">{{ name }}</option>
            </select>

            <input
              v-model.number="item.qty"
              type="number"
              min="1"
              class="w-full rounded-2xl border border-black/10 bg-white px-4 py-3 text-sm outline-none focus:ring-4 focus:ring-black/10"
              placeholder="Cantitate"
            />

            <input
              :value="formatItemTotal(item)"
              type="number"
              readonly
              class="w-full rounded-2xl border border-black/10 bg-black/5 px-4 py-3 text-sm outline-none focus:ring-4 focus:ring-black/10"
            />

            <button
              type="button"
              class="rounded-full border border-black/10 bg-white px-3 py-2 text-xs font-semibold uppercase tracking-wider text-black/60 transition hover:-translate-y-0.5 disabled:opacity-50"
              :disabled="form.items.length === 1"
              @click="removeItem(index)"
            >
              Sterge
            </button>
          </div>
        </div>

        <button
          type="button"
          class="mt-3 rounded-full border border-black/10 bg-white px-4 py-2 text-xs font-semibold uppercase tracking-wider text-black/70 transition hover:-translate-y-0.5"
          @click="addItem()"
        >
          Adauga produs
        </button>
      </div>

      <div class="mt-4">
        <label class="text-xs font-semibold text-black/60">Total (RON)</label>
        <input
          :value="formatOrderTotal"
          type="number"
          min="0"
          readonly
          class="mt-2 w-full rounded-2xl border border-black/10 bg-black/5 px-4 py-3 text-sm outline-none focus:ring-4 focus:ring-black/10"
        />
      </div>

      <div class="mt-5">
        <label class="text-xs font-semibold text-black/60">Note</label>
        <textarea
          v-model.trim="form.notes"
          rows="3"
          class="mt-2 w-full rounded-2xl border border-black/10 px-4 py-3 text-sm outline-none focus:ring-4 focus:ring-black/10"
          placeholder="ex: fără ceapă, extra sos..."
        />
      </div>

      <div class="mt-6 flex items-center gap-3">
        <button
          type="submit"
          :disabled="loading"
          class="rounded-full bg-black px-6 py-3 text-sm font-semibold text-white shadow-sm hover:bg-black/90 disabled:opacity-60"
        >
          {{ loading ? "Se trimite..." : "Trimite comanda" }}
        </button>

        <p class="text-sm text-black/50" v-if="lastOrderId">
          ID: <span class="font-semibold text-black/70">{{ lastOrderId }}</span>
        </p>
      </div>
    </form>
  </section>
</template>

<script setup>
import { computed, reactive, ref, watch } from "vue";

const props = defineProps({
  itemOptions: {
    type: Array,
    default: () => [
      { name: "Cheeseburger Family", price: 30 },
      { name: "Burger Quattro Formaggi", price: 30 },
      { name: "Burger Picant", price: 30 },
      { name: "Burger Crispy de pui", price: 30 },
      { name: "Cartofi prăjiți", price: 12 },
    ],
  },
  initialItemName: {
    type: String,
    default: "",
  },
});

const API = "https://western-helenka-dreamfamily-41619d47.koyeb.app";

const loading = ref(false);
const lastOrderId = ref("");

const toast = reactive({
  show: false,
  title: "",
  message: "",
  timer: null,
});

function showToast(title, message) {
  toast.title = title;
  toast.message = message;
  toast.show = true;

  if (toast.timer) clearTimeout(toast.timer);
  toast.timer = setTimeout(() => (toast.show = false), 3500);
}

const normalizedItems = computed(() =>
  props.itemOptions.map((item) => {
    if (typeof item === "string") {
      return { name: item, price: 0 };
    }
    return { name: item.name, price: Number(item.price) || 0 };
  })
);

const itemNames = computed(() => normalizedItems.value.map((item) => item.name));

const createItem = (name) => ({
  name: name || itemNames.value[0] || "",
  qty: 1,
});

const form = reactive({
  customerName: "",
  phone: "",
  items: [createItem(props.initialItemName || itemNames.value[0])],
  notes: "",
});

watch(
  () => props.initialItemName,
  (nextName) => {
    if (nextName && form.items.length) {
      form.items[0].name = nextName;
      return;
    }
    if (form.items.length && !itemNames.value.includes(form.items[0].name)) {
      form.items[0].name = itemNames.value[0] || "";
    }
  }
);

const getPrice = (name) => {
  const selected = normalizedItems.value.find((item) => item.name === name);
  return selected?.price ?? 0;
};

const getQty = (qty) => Math.max(1, Number(qty) || 1);

const orderTotal = computed(() =>
  form.items.reduce((sum, item) => sum + getPrice(item.name) * getQty(item.qty), 0)
);

const formatItemTotal = (item) => getPrice(item.name) * getQty(item.qty);
const formatOrderTotal = computed(() => orderTotal.value);

const addItem = () => {
  form.items.push(createItem(""));
};

const removeItem = (index) => {
  if (form.items.length === 1) return;
  form.items.splice(index, 1);
};

async function submitOrder() {
  loading.value = true;
  lastOrderId.value = "";

  try {
    const payload = {
      customerName: form.customerName,
      phone: form.phone,
      items: form.items.map((item) => ({
        name: item.name,
        qty: getQty(item.qty),
        price: getPrice(item.name),
      })),
      notes: form.notes,
      total: Number(orderTotal.value || 0),
    };

    const r = await fetch(`${API}/api/orders`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
    });

    const data = await r.json().catch(() => ({}));

    if (!r.ok) {
      showToast("Eroare ❌", data?.error || "Nu s-a putut trimite comanda.");
      return;
    }

    lastOrderId.value = String(data?.order?._id || "").slice(-6);

    showToast("Comandă trimisă ✅", "Bucătăria a primit comanda.");
    // reset form (păstrăm primul produs ales)
    form.customerName = "";
    form.phone = "";
    form.notes = "";
    form.items = [createItem(form.items[0]?.name)];
  } catch (e) {
    showToast("Eroare ❌", "Server indisponibil sau CORS.");
  } finally {
    loading.value = false;
  }
}
</script>
